html {
    height: 70%;
}

body {
    padding: 0;
    margin: 0;
    height: 70%;
}

.game {
    padding-top: 10px;
    padding-left: 15%;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
    position: relative;
    .board {
        float: left;
    }
    .buttonBlock {
        float: right;
    }
    h4 {
        // font-family: 'FGG';
        font-size: 24px;
        margin-bottom: 20px;
    }

    .next-step,
    .winner {
        position: absolute;
        top: 40px;
        right: 20px;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;

        span {
            // font-family: 'FGG';
            margin-right: 10px;
        }

        button {
            border-style: none;
            outline: 0;
            width: 26px;
            height: 26px;
            border-radius: 13px;
        }
    }

    .board {
        background: url('./assets/images/board_bg.png');
        background-size: cover;
        background-color: #56bc48;
        border-color: #2ea738;
        border-radius: 10px;
        position: relative;

        .chess-wrap {
            position: absolute;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            button.chess {
                border-style: none;
                outline: none;
                padding: 0;
                background-size: contain;
            }

            .green-dot {
                position: absolute;
                background-image: radial-gradient(at 80px 80px, rgba(0, 255, 0, 0.7), rgba(0, 255, 0, 0.7));
            }

            .red-dot {
                position: absolute;
                background-image: radial-gradient(at 80px 80px, rgba(255, 0, 0, 0.7), rgba(255, 0, 0, 0.7));
            }
        }
    }
    #step {
        padding: 8px 15px;
        margin-left: 10px;
        font-size: 20px;
        border-radius: 10px;
    }
    #speed {
        padding: 8px 15px;
        font-size: 20px;
        border-radius: 10px;
    }
    label {
        font-size: 25px;
    }

    .btns {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        align-items: center;
        width: 90%;
        margin-top: 25px;
        margin-bottom: 25px;

        button {
            outline: none;
            border: 1px solid #333;
            color: #333;
            font-size: 20px;
            padding: 8px 15px;
            border-radius: 6px;
        }
    }
}
